	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 11 - Les gènes Drumel","texte":{"childMarkdownRemark":{"html":"<p>Darakei savait que Wamien arriverait avec ceux qu’il avait demandé à voir pour faire une prise de sang et finissait de préparer la salle où les échantillons seraient prélevés.</p>\n<p>Julius : Monsieur Drumel, Wamien et ses amis arrivent, ils vous attendent à la réception.<br>\nDarakei : Merci, Julius, peux-tu aller les chercher et les faire venir ici s’il te plaît?<br>\nJulius : Tout de suite Monsieur.</p>\n<p>Julius partit à la rencontre de Wamien et des autres. Darakei essayait de rester professionnel malgré l’absence de son fils. Éléa faisait son possible pour retrouver leur enfant.</p>\n<p>Wamien arriva avec Sam, Gabriel, Hiro, Sarah et Sandrine. Darakei leur sourit à tous.</p>\n<p>Darakei : Bonjour à tous, désoler de vous avoir fait faire tout ce chemin jusqu’ici.<br>\nWamien : Ne t’en fait pas Darakei, on est là pour t’aider dans tes recherches.<br>\nDarakei : Merci, je vous invite à vous installer chacun votre tour sur la chaise. Qui veut y aller en premier?<br>\nSam : Je vais y aller, je déteste avoir une prise de sang alors, autant m’en débarrasser au plus vite.</p>\n<p>Darakei installe Sam pour lui faire la prise de sang et Sam prend une grande inspiration avant de fermer les yeux pour que Darakei procède au prélèvement sanguin. Wamien regarde les autres.</p>\n<p>Wamien : Qui y va après?<br>\nHiro : Je vais y aller.<br>\nSarah : Moi après et Sandrine qui me suit.<br>\nSandrine : Ne décide pas à ma place, sœurette.<br>\nGabriel : J’y vais en dernier.</p>\n<p>Darakei termine avec Sam et lui place le bandage.</p>\n<p>Darakei : Suivant.</p>\n<p>Hiro va se placer et donne une tape sur le dos à Sam.</p>\n<p>Hiro : T’es courageux Sam.<br>\nSam : Merci Hiro.</p>\n<p>Hiro va s’installer et Darakei procède au prélèvement sanguin.</p>\n<p>Gabriel : Sinon Monsieur Drumel, c’est pour quelle raison les prises de sang?<br>\nDarakei : J’ai une nouvelle théorie au sujet de la manière d’identifier les gens qui peuvent avoir les gènes des Nouveaux Humains.<br>\nGabriel : Oh! Cool!<br>\nDarakei : Effectivement, je ne peux naturellement pas tout expliquer comment j’étudie tout ça, mais je peux vous dire que vous allez tous m’aider en donnant un peu de sang.<br>\nWamien : Aller Sarah, c’est ton tour.</p>\n<p>Sarah vient se placer à côté d’Hiro. Hiro devient blême.</p>\n<p>Sarah : Oh? Ça va Hiro?<br>\nDarakei : Je termine la prise de sang et après met ta tête entre tes jambes.<br>\nHiro : Co-comment ça?</p>\n<p>Darakei finit le prélèvement de Hiro et prit sa tête pour lui mettre entre ses jambes.</p>\n<p>Darakei : Si je te laissais assis comme ça, tu serais tombé dans les pommes. Et je ne pense pas que ça soit ce que l’on souhaite.</p>\n<p>Hiro était un peu mal à l’aise de se retrouver dans une position de la sorte. Sarah lui caressa le dos et Darakei redressa Hiro.</p>\n<p>Hiro : Ça va mieux, je crois.<br>\nDarakei : Bien, laisse la place à Sarah alors.</p>\n<p>Hiro se lève et va rejoindre les autres. Sarah prend sa place et les autres prises de sang se déroulent sans problème.</p>\n<p>Darakei : Wamien, tu veux venir avec moi, j’ai à te parler en privé.<br>\nWamien : Aucun problème Darakei, je te suis. Les amis, allez m’attendre à la réception.</p>\n<p>Darakei va vers son bureau et je le suis. On arrive dans son bureau et ferme la porte après que j’y ai entré.</p>\n<p>Darakei : Assieds-toi, ça risquerait de te déboussoler un peu ce que j’ai à te dire.</p>\n<p>Je me demande ce qu’il a à me dire de si important, mais je vais m’asseoir sur une des chaises de son bureau.</p>\n<p>Darakei : Donc, par où commencer? Le but de GenCorp, tu le connais?<br>\nWamien : Identifier les Nouveaux Humains ou possibles Nouveaux Humains et leur permettre de débloquer leurs pouvoirs?<br>\nDarakei : À peu près, mais, il y a plus que ça. Lorsque les enfants ont l’âge de quatre ans, nous pouvons prendre un échantillon de sang si nous avons le consentement des parents ou tuteurs. Donc, quand tu as eu quatre ans, nous avons eu un échantillon de ton sang et nous avons identifié le potentiel des gènes des nouveaux humains. Nous te surveillons donc depuis le moment, sans intervenir directement ou indirectement.<br>\nWamien : D’accord, c’est un peu particulier, mais je comprends l’idée. C’est sûr que d’enfermer ceux qui ont leurs pouvoirs activés n’est pas très diplomate.<br>\nDarakei : Je sais, mes supérieurs ne sont pas trop dans l’idée de laisser des gens fraîchement activés se promener à l’air libre.<br>\nWamien : Attends, attends, tu as des supérieurs?<br>\nDarakei : Oui, environs une dizaine de personnes plus hautes que moi, mais au laboratoire de Damonia, je suis le plus haut gradé dans le continent.<br>\nWamien : Pendant tout ce temps, je pensais que c’était toi le chef de GenCorp et j’ai mis la plupart de tes actions sur ton dos. Et je m’en excuse.<br>\nDarakei : Ça va, ne t’en fais pas. Je te comprends. Tu voulais protéger les autres, c’est une excellente chose, tu sais?<br>\nWamien : Bien sûr Darakei, bien sûr que je voulais protéger mes amis.<br>\nDarakei : Je suis fier de ce que tu fais avec La Guilde et ne t’en fais pas, nous trouverons un moyen de remettre les Black Eagles derrière les barreaux.<br>\nWamien : Et La Légion qui est encore en liberté aussi.<br>\nDarakei : Aussi. Allez, je te laisse repartir avec tes amis, j’ai leurs échantillons à étudier.<br>\nWamien : D’accord Darakei, passe une bonne journée.<br>\nDarakei : Merci et bonne route à toi et La Guilde.</p>\n<p>Je me lève et sors du bureau de Darakei pour aller rejoindre les autres à la réception.</p>\n<p>Hiro : Ça va? Tu as l’air un peu désorienté.<br>\nWamien : Oui oui, ça va. Allez, nous avons une longue route à faire pour rentrer à la maison.</p>\n<p>Julius nous regarde partir. Je n’aime pas sa petite tête d’assistant. Peut-être qu’une impression.</p>"}},"chapitreAvant":"chapitre-10-william","chapitreApres":"chapitre-12-vol","nomRoman":"le-premier-cyborg-tome-2","slug":"chapitre-11-les-genes-drumel","codeChapitre":"H0002C011","equivalentUrl":"/stories/chapter/chapter-11-the-drumel-genes"}},"pathContext":{"slug":"chapitre-11-les-genes-drumel"}}

